import { Controller } from '@ali/4ever-cangjie';
import equal from 'fast-deep-equal';
import { Callout, isCalloutParagraph } from '../utils';

export function getCalloutStartBlock(controller: Controller, block: Callout) {
  if (!isCalloutParagraph(block)) {
    return null;
  }

  const { document } = controller.value;

  let startBlock = block;
  let previousBlock = document.getPreviousBlock(startBlock.key);

  while (
    previousBlock &&
    isCalloutParagraph(previousBlock) &&
    equal(startBlock.data.calloutPr, previousBlock.data.calloutPr)
  ) {
    startBlock = previousBlock;
    previousBlock = document.getPreviousBlock(startBlock.key);
  }

  return startBlock;
}

export function getCalloutEndBlock(controller: Controller, block: Callout) {
  if (!isCalloutParagraph(block)) {
    return null;
  }

  const { document } = controller.value;

  let endBlock = block;
  let nextBlock = document.getNextBlock(endBlock.key);

  while (
    nextBlock &&
    isCalloutParagraph(nextBlock) &&
    equal(endBlock.data.calloutPr, nextBlock.data.calloutPr)
  ) {
    endBlock = nextBlock;
    nextBlock = document.getNextBlock(endBlock.key);
  }

  return endBlock;
}
